<template>
	<div >
		<!--头部提示信息-->
		<header class="top_tips">
			<span class="tips_num" v-if="componentType=='home'">{{getLevel}}</span>
			<span class="tips_num" v-if="componentType=='item'">题目{{getQuestionNum}}</span>
		</header>
		<!--首页中间部分-->
		<!--第一个div是首页的中间部分 如果类型是home组件就显示-->
		<div v-if="componentType=='home'">
			<div class="home_logo common_middle" >
				
			</div>
			<router-link to='/item' class="start"></router-link>
		</div>
		
		<!--中间的item，也就是答题部分-->
		<div v-if="componentType=='item'">
			<div class="question_container common_middle">
				<div class="qutesiton_content">
					<header class="question_title">{{getQuestionList[getQuestionNum-1].topic_name}}</header>
					<ul class="answer_list">
						<li class="answer_list_item" v-for="(item,index) in getQuestionList[getQuestionNum-1].topic_answer" @click="clickOption(index,item.topic_answer_id)">
							<span class="choose_num" :class="{choosed_num:optionNum==index}">{{chooseOption(index)}}</span>
							<span class="answer_detail">{{item.answer_name}}</span>
						</li>
						<!--<li class="answer_list_item">
							<span class="choose_num choosed_num">B</span>
							<span class="answer_detail">A是正确答案</span>
						</li>
						<li class="answer_list_item">
							<span class="choose_num">A</span>
							<span class="answer_detail">A是正确答案</span>
						</li>
						<li class="answer_list_item">
							<span class="choose_num">A</span>
							<span class="answer_detail">A是正确答案</span>
						</li>-->
						
						
					</ul>
				</div>
			</div>
			<!--使用v-fi v-else 或者v-else-if  他们中间不能有任何其他标签-->
			<div class="next_question common_button_style" v-if="getQuestionNum<getQuestionList.length" @click="nextQuestion">
			</div>
			<div class="common_button_style submit" v-else  @click="gotoScore">
			</div>
			
			
		</div>
	</div>
</template>

<script>
	export default{
		name:'itemcontainer',
		props:{
			componentType:{
				type:String
			}
		},
		data(){
			return {
				optionNum:null,//为了添加选中的样式获取的下标
				myChooseId:null,//我选择的id
			}
		},
		methods:{
			gotoScore(){
				
				if(this.optionNum!==null){
					this.optionNum=null;
//					this.$store.commit('addQuestionNum',this.myChooseId);
					//触发action
					this.$store.dispatch('addQuestionNum',this.myChooseId);
					this.$router.push('score')
				}else{
					alert("你还没有选择呢？")
				}
			},
			chooseOption(index){
				switch(index){
					case 0:
						return 'A';
					case 1:
						return 'B';
					case 2:
						return 'C';
					case 3:
						return 'D';
				}
			},
			clickOption(index,myChooseId){
				this.optionNum=index;
				this.myChooseId=myChooseId;
			},
			nextQuestion(){
				if(this.optionNum!==null){
					this.optionNum=null;
//					this.$store.commit('addQuestionNum',this.myChooseId);
					//触发action
					this.$store.dispatch('addQuestionNum',this.myChooseId)
				}else{
					alert("你还没有选择呢？")
				}
				
			}
		},
		computed:{
//			获取周数
			getLevel(){
				return this.$store.state.level;
			},
			//获取当前的题号
			getQuestionNum(){
				return this.$store.state.questionNum;
			},
			//获取题目列表
			getQuestionList(){
				return this.$store.state.questionList;
			}
			
		},
		created(){
			this.$store.commit('initData');
		}
	}
</script>

<!--scoped 样式只在本组件中起作用 less,是使用的css语法-->
<style scoped="scoped" lang="less">
	/*头部提示开始*/
	.top_tips{
		width: 4rem;
	    height: 6rem;
	    background: url(../image/WechatIMG2.png) no-repeat;
	    background-size: 100% 100%;
	    position: absolute;
	    right: 1.5rem;
	    .tips_num{
	    	font-size: 0.65rem;
		    display: inline-block;
		    width: 3.8rem;
		    height: 1.4rem;
		    position: absolute;
		    top: 4.2rem;
		    text-align: center;
		    line-height: 1.4rem;
		    color: #9F9164;
		    font-weight: lighter;
	    }
	}
	/*头部提示结束*/
	
	/*首页中间开始*/
	.home_logo{
		
	    background: url(../image/1-2.png) no-repeat;
	    background-size: 100% 100%;
	  
	}
	.common_middle{
		 width: 12rem;
	    height: 10rem;
	    position: absolute;
	    top: 6rem;
	    left: 50%;
	    margin-left: -6rem;
	}
	.start{
		width: 4rem;
	    height: 2rem;
	    background: url(../image/1-4.png) no-repeat;
	    background-size: 100% 100%;
	    display: block;
	    position: absolute;
	    top: 16rem;
	    left: 50%;
	    margin-left: -2rem;
	}
	/*首页中间结束*/
	
	
	/*答题部分开始*/
	.question_container{
		background: url(../image/2-1.png) no-repeat;
		background-size: 100% 100%;
		.question_title{
			font-size: 0.65rem;
		    width: 8rem;
		    position: absolute;
		    top: 2rem;
		    left: 2.5rem;
		}
		.answer_list{
			position: absolute;
    		top: 3rem;
   		 	left: 2.5rem;
   		 	.choose_num{
   		 		font-size: 0.6rem;
			    display: inline-block;
			    border: 1px solid gray;
			    width: 0.8rem;
			    height: 0.8rem;
			    border-radius: 50%;
			    text-align: center;
			    line-height: 0.8rem;
   		 		
   		 	}
   		 	.choosed_num{
   		 		border: 1px solid yellow;
   		 		background-color: yellow;
   		 		color: black;
   		 	}
   		 	.answer_detail{
   		 		font-size: 0.6rem;
   		 	}
		}
	}
	.common_button_style{
		 position: absolute;
	    top: 16rem;
	    left: 50%;
	    margin-left: -2rem;
	     width: 4rem;
	    height: 2rem;
	}
	.next_question{
	    background: url(../image/2-2.png) no-repeat;
	    background-size: 100% 100%;
	}
	.submit{
		background: url(../image/3-1.png) no-repeat;
	    background-size: 100% 100%;
	}
	
	/*答题部分结束*/
</style>


