<template>
	<div >
		<!--分数-->
		<div class="my_score_container">
			<p class="my_score">
				<span class="fenshu">{{score}}</span>
				<span class="fenshu">分!</span>
			</p>
			<p class="pingjia">{{tips}}</p>
		</div>
		<!--分享按钮-->
		<div class="share" @click="isShow=!isShow">
			
		</div>
		<!--遮罩层-->
		<div class="mask" v-show="isShow"  @click="isShow=!isShow">
			<img src="../image/5-2.png" class="share_img" />
		</div>
	</div>
</template>

<script>
	export default {
		name:'score',
		data(){
			return{
				isShow:false,
				score:0,
				tips:""
			}
		},
		created(){
			
			var myAList=this.$store.state.myAnsers;
//			var rightList=this.$store.state.rightAnswers;
//			for(var i=0;i<rightList.length;i++){
//				if(rightList[i]==myAList[i]){
//					this.score+=20;
//				}
//			}
			var that=this;
			//第二种方式for循环
//			this.$store.state.rightAnswers.forEach(function(item,index){
//				
//				if(myAList[index]==item){
//						that.score+=20;
//				}
//				
//			})

			this.$store.state.rightAnswers.forEach((item,index)=>{
				if(myAList[index]==item){
						that.score+=20;
				}
			})
			
			if(this.score>=80){
				this.tips="你很聪明，脑袋瓜可以赶上图灵了";
			}else if(this.score>=60){
				this.tips="你很聪明，脑袋瓜可以赶上爱因斯坦了"
			}else if(this.score>=40){
				this.tips="你已经不错了，脑袋瓜可以赶上牛顿了"
			}else if(this.score>=20){
				this.tips="很好很好，再接再厉，下次"
			}else{
				this.tips="你脑子很灵光，就是不适合做学问"
			}
			
		}
	}
</script>

<style lang="less">
	/*重写body演示,换一张背景图*/
	body{
		background: url(../image/4-1.jpg) no-repeat;
		background-size: 100% 100%;
	}
	/*分数开始*/
	.my_score_container{
		background: url(../image/4-2.png) no-repeat;
		background-size: 100% 100%;
	    width: 10rem;
	    height: 8rem;
	    position: absolute;
	    top: 6rem;
	    left: 50%;
	    margin-left: -5rem;
	    .my_score{
	    	    text-align: center;
			    position: absolute;
			    top: 4rem;
			    width: 9rem;
			    margin-left: 0.65rem;
	    	.fenshu{
	    		color: red;
	    		font-weight: bold;
	    	}
	    }
	    .pingjia{
	    	color: palevioletred;
		    font-size: 0.7rem;
		    position: absolute;
		    top: 5.5rem;
		    text-align: center;
		    margin-left: 0.8rem;
	    }
	}
	/*分数结束*/
	
	
	/*分享按钮*/
	.share{
		background: url(../image/4-3.png) no-repeat;
		    background-size: 100% 100%;
		    width: 6rem;
		    height: 3rem;
		    position: absolute;
		    top: 15rem;
		    left: 50%;
		    margin-left: -3rem;
	}
	/*遮罩层*/
	.mask{
		background: url(../image/5-1.png) no-repeat;
		background-size: 100% 100%;
	    width: 100%;
	    height: 100%;
	    position: absolute;
	    top: 0;
	    bottom: 0;
	    opacity: 0.8;
	    .share_img{
	    	width: 12rem;
		    height: 10rem;
		    position: absolute;
		    top: 0.3rem;
		    left: 50%;
		    margin-left: -6rem;
	    }
	}
</style>


