import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'
import home from '@/components/home'
import item from '@/components/item'
import score from '@/components/score'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'home',
      component: home
    },
    {
      path: '/item',
      name: 'item',
      component: item
    },,
    {
      path: '/score',
      name: 'score',
      component: score
    },
    
  ]
})



